.nav
{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height:30px;
    z-index: 1 ;

    transition-timing-function: ease-in ;
    transition: all 0.5s;
}
.nav__content
{
    display: flex;
    justify-content: space-between;
}
.nav__black
{
    background-color: black;
}
.nav__logo
{
    position: fixed;
    left: 0;
    width:80px;
    padding-left: 10px;
    object-fit: contain;
    cursor: pointer;
}
.nav__avatar
{
    position: fixed;
    right: 20px;
    width: 30px;
}